/**
 * @module M/plugin/XYLocator
 */
import 'assets/css/xylocator';
import XYLocatorControl from './xylocatorcontrol';

export default class XYLocator extends M.Plugin {
  /**
   * @classdesc
   * Main facade plugin object. This class creates a plugin
   * object which has an implementation Object
   *
   * @constructor
   * @extends {M.Plugin}
   * @param {Object} impl implementation object
   * @api stable
   */
  constructor(options) {
    super();
    /**
     * Facade of the map
     * @private
     * @type {M.Map}
     */
    this.map_ = null;

    /**
     * Array of controls
     * @private
     * @type {Array<M.Control>}
     */
    this.controls_ = [];

    // Param: [projection1, projection2, etc.]
    // Projection1 =  {title, code, units(m/d)}

    /**
     * Array of object
     * @private
     * @type {Array<Object>}
     */
    this.projections_ = options.projections;
    if (typeof options.projections === 'string') {
      try {
        this.projections_ = JSON.parse(this.projections_);
      } catch (e) {
        this.projections_ = [];
      }
    }

    /**
     * Position of the Plugin
     * Posible values: TR | TL | BL | BR
     * @type {Enum}
     */
    this.position_ = options.position || 'TL';
    if (this.position_ === 'TL' || this.position_ === 'BL') {
      this.positionClass_ = 'left';
    }
  }

  /**
   * This function adds this plugin into the map
   *
   * @public
   * @function
   * @param {M.Map} map the map to add the plugin
   * @api stable
   */
  addTo(map) {
    this.map_ = map;
    this.control_ = new XYLocatorControl(this.projections_);
    this.panel_ = new M.ui.Panel('M.plugin.XYLocator.NAME', {
      collapsible: true,
      className: `m-xylocator ${this.positionClass_}`,
      collapsedButtonClass: 'g-cartografia-posicion3',
      position: M.ui.position[this.position_],
      tooltip: 'BUSCAR POR COORDENADAS',
    });
    // FIRE EVENT
    this.controls_.push(this.control_);
    this.controls_[0].on('xylocator:locationCentered', (data) => {
      this.fire('xylocator:locationCentered', data);
    });
    this.panel_.addControls(this.controls_);
    map.addPanels(this.panel_);
  }

  /**
   * This function destroys this plugin
   *
   * @public
   * @function
   * @api stable
   */
  destroy() {
    this.map_.removeControls([this.control_]);
    [this.map_, this.control_, this.panel_] = [null, null, null];
  }

  /**
   * This function return the control of plugin
   *
   * @public
   * @function
   * @api stable
   */
  getControls() {
    const aControl = [];
    aControl.push(this.controls_);
    return aControl;
  }

  /**
   * @getter
   * @public
   */
  get name() {
    return 'xylocator';
  }
}
